body {
    background-color: rgb(245, 245, 245);
}
.namecard {
    margin-top: -1.5rem;
    height: 950px;
    width: 100%;
    background-image: url('images/background2.jpeg');
    background-size: cover;
}
.info {
    margin-top: 0rem;
    display: block;
    padding: 1rem;
    position: relative;
    top: 15rem;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    background-color: #ffffff;
    width: 60%;
    box-shadow: 0px 0px 15px 5px rgba(39,43,55,0.15);
}
.info .self {
    margin-top: -6.5rem;
    margin-left: auto;
    margin-right: auto;
    height: 175px;
    width: 175px;
    border-radius: 50%;
    border: 5px solid #ffffff;
    box-shadow: 0px 0px 15px 5px rgba(39,43,55,0.15);
}

.namecard p {
    padding-top: 0.5rem;
}
.namecard .name {
    padding-top: 2rem;
    text-transform: uppercase;
    color: black;
    font-size: 38px;
}
.namecard .handle {
    margin-top: -1rem;
    color: gray;
    font-size: 20px;
    padding-bottom: 0.5rem;
}
.namecard .dash {
    background-color: lightsalmon;
    display: block;
    margin-right: auto;
    margin-left: auto;
    margin-top: -1rem;
    height: 3px;
    width: 75px;
    text-align: center;
}
.namecard .city {
    padding-top: 1rem;
    font-style: italic;
}
.namecard .job {
    text-align: justify;
    padding: 1rem;
    line-height: 2rem;
    font-weight: bold;
}

@media screen and (min-width: 1171) {
 }
      
@media screen and (max-width: 500px) {
    .info {
        padding: 0;
        position: relative;
        margin-left: auto;
        margin-right: auto;
        width: 90%;
         } }

@media screen and (max-width: 768px) {
    .info {
        padding: 0;
        position: relative;
        margin-left: auto;
        margin-right: auto;
        width: 90%;
        }
}

@media screen and (min-width: 769px) and (max-width: 1170px) {
    .info {
        padding: 0;
        position: relative;
        margin-left: auto;
        margin-right: auto;
        width: 90%;
        }
}