.container {
    margin: 0.5rem;
    display: inline-block;
}
.navigation-menu .socialLinks {
    position: absolute;
    top: 10px;
    right: 10px;
    align-content: right;
    display: inline-block;
    background-color: transparent;
    font-size: 10px;
    color: white;
}
.socialLinks a ion-icon {
    padding-right: 1.5rem;
    color: white;
    height: 30px;
    width: 30px;
}
.socialLinks a ion-icon:hover {
    color: black;
}
.menu-btn {
    padding-top: 0.5rem;
    position: fixed;
    left: 20px;
    top: 10px;
    display: inline-block;
    z-index: 3;
    cursor: pointer;
    transition: all 0.5s easeOut;
}
.menu-btn .btn-line {
    width: 28px;
    height: 3px;
    margin: 0 0 5px 0;
    background: lightsalmon;
    transition: all 0.5s easeOut;
}

.menu-btn.close {
    transform: rotate(180deg);
}
.menu-btn.close .btn-line:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
    transition: 0.5s all ease-out;
}
.menu-btn.close .btn-line:nth-child(2) {
    opacity: 0;
}
.menu-btn.close .btn-line:nth-child(3) {
    transform: rotate(-45deg) translate(7px, -6px);
    transition: 0.5s all ease-in;
}
        
.menu-btn.open {
    transform: rotate(180deg);
}
.menu-btn.open .btn-line:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
    background: lightsalmon;
    transition: all 0.5s ease-in;

}
.menu-btn.open .btn-line:nth-child(2) {
    opacity: 0;
}
.menu-btn.open .btn-line:nth-child(3) {
    transform: rotate(-45deg) translate(7px, -6px);
    background: lightslamon;
    transition: all 0.5s ease-in;

}
.nav-item:nth-child(1) {
    transition-delay: 0.1s; }
  
.nav-item:nth-child(2) {
    transition-delay: 0.2s; }
  
.nav-item:nth-child(3) {
    transition-delay: 0.3s; }
  
.nav-item:nth-child(4) {
    transition-delay: 0.4s; }

.menu {
    position: fixed;
    display: block;
    top: 0;
    left: 0;
    opacity: 0.9;
    width: 100%;
    z-index: 2;
    visibility: hidden; }
    .menu.show {
      visibility: visible; }
    .menu-nav {
      display: flex;
      flex-flow: column;
      align-items: center;
      justify-content: center;
      float: left;
      width: 250px;
      height: 100vh;
      overflow: hidden; }
    .menu-nav {
      background: black;
      background-size: cover;
      padding: 0;
      list-style: none;
      transform: translate3d(-100%, 0, 0);
      transition: all 0.5s ease-out; }
      .menu-nav.show {
        transform: translate3d(0, 0, 0); }

      .menu-nav .portrait {
        margin-top: 75px;
        margin-bottom: 25px;
        width: 200px;
        height: 200px;
        background-image: url("images/self.jpg");
        border-radius: 50%;
        background-size: cover;
        border: solid 2px black}
    .menu .nav-item {
      transform: translate3d(100px, 0, 0);
      transition: all 0.5s ease-out; }
      .menu .nav-item.show {
        transform: translate3d(0, 0, 0); }
      .menu .nav-item.current > a {
        color: lightsalmon; }
    .menu .nav-link {
      display: inline-block;
      position: relative;
      font-size: 18px;
      text-transform: uppercase;
      padding: 0.75rem 0;
      color: white;
      text-decoration: none;
      transition: all 0.5s ease-out; }
      .menu .nav-link:hover {
        color: lightsalmon; }


.menu .contactinfo {
    margin: 0;
    padding-top: 2rem;
    display: inline-block;
}

.menu .contactinfo span {
    display: inline-block;
    background-color:white;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    color: black;
    font-size: 1rem;
    border: 0.25rem solid black;
    transition: 0.5s ease-out;
}

.menu .contactinfo span ion-icon {
    margin-left: auto;
    margin-right: auto;
    position: relative;
    contain: none;
    display: block;
    font-size: 1rem;
    padding: 1rem;
}

.menu .contactinfo span:hover {
    background-color:black;
    border-radius: 50%;
    color: white;
    border: 0.25rem solid rgb(233, 126, 55);
    transition: 0.2s ease-in;
}

.contactinfo h4{
    font-size: 1rem;
    color: lightsalmon;
}
.contactinfo p {
    color: white;
    padding-bottom: 1rem;
    font-size: 1rem;
}
.contactinfo .email {
    text-align: center;
}
.contactinfo .phone {
    text-align: center;
}