* {
    box-sizing: border-box;
}

.back {
    height: 950px;
    margin-top: 0rem;
    background-image: url('images/skill2.png');
    background-size: cover;
    width: 100%;
    color: black;
    display: grid;
    grid-gap: 5px;
    text-align: center;
    justify-items: center;
}
.back .box{
    display: block;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
}
.me {
    position: relative;
    top: 20px;
    font-size: 100px;
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 2;
}
.job1 {
    background-color: #ffffff;
    width: 20rem;
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
    box-shadow: 0px 0px 15px 5px rgb(97, 96, 96);
    border-radius: 3px;
    border: 5px solid #ddd;
    border-bottom: 10px solid lightsalmon;
    margin-bottom: 3rem;
}
.job2 {
    background-color: #ffffff;
    width: 20rem;
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;
    box-shadow: 0px 0px 15px 5px rgb(97, 96, 96);
    border-radius: 3px;
    border: 5px solid #ddd;
    border-bottom: 10px solid lightsalmon;
    margin-bottom: 3rem;

}
.job3 {
    background-color: #ffffff;
    width: 20rem;
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 3;
    box-shadow: 0px 0px 15px 5px rgb(97, 96, 96);
    border-radius: 3px;
    border: 5px solid #ddd;
    border-bottom: 10px solid lightsalmon;
    margin-bottom: 3rem;
}
.job1image {
    background-image: url("./images/job1.jpeg");
    background-size: cover;
    height: 10rem;
}
.job2image {
    background-image: url("./images/job2.png");
    background-size: cover;
    height: 10rem;
}
.job3image {
    background-image: url("./images/job3.jpg");
    background-size: cover;
    height: 10rem;
}
.title1 {
    margin-top: 2rem;
    color: black;
    padding: 0.5rem;
    width: 20rem;
    border: 2px solid white;
    font-size: 25px;
    font-weight: bolder;
    background-color: lightsalmon;
}
.box .title {
    margin-top: 15px;
    color: black;
    font-weight: bold;
    font-size: 18px;
    text-transform: uppercase;
}
.box .location {
    margin-top: -7px;
    font-style: italic;
    font-size: 13px;
}
.box .date {
    margin-top: -15px;
    font-size: 13px;
    font-style: italic;
    color: grey;
}
.box .details {
    text-align: justify;
    font-size: 15px;
    padding: 0.5rem 1.5rem;
}
.box .dash2 {
    margin-top: 5px;
    background-color: white;
    width: 100%;
    height: 3px;
    color: white;
    margin-bottom: 2rem;
}
.box .dash3 {
    background-color: lightsalmon;
    height: 2px;
    width: 100px;
    position: relative;
    top: -15px;
    margin-right: auto;
    margin-left: auto;
}
     
@media screen and (max-width: 500px) {
    .back {
        height: auto;
    }
   .box {
    text-align: center;
    justify-content: center;
    display: block;
    grid-template-columns: 1fr; 
    grid-template-rows: repeat(4, 1fr);
    position: relative;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    }
    .box .title1 {
        width: 100%;
    }
    .me {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 1;
        grid-row-end: 2;
    } 
    .job1 {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 2;
        grid-row-end: 3;
    }
    .job2 {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 3;
        grid-row-end: 4;
    }
    .job3 {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 4;
        grid-row-end: 5;
    }
}

