.workPage {
    height: 950px;
    width: 100%;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0rem;
    text-align: center;
    background-color: black;
    background-image: url('images/projectbackground.jpg');
    background-size: cover;
}
.workPage .workTitle {
    position: relative;
    top: 4rem;
    margin-left: auto;
    margin-right: auto;
    color: black;
    padding: 0.5rem;
    width: 20rem;
    border: 2px solid white;
    font-size: 25px;
    font-weight: bolder;
    background-color: lightsalmon;
    text-transform: uppercase;
}
.work-container {
    width: 50rem;
    position: relative;
    top: 8rem;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}
.workPage .dash2 {
    position: relative;
    top: 5rem;
    margin-left: auto;
    margin-right: auto;
    background-color: white;
    width: 20rem;
    height: 3px;
    margin-bottom: 2rem;
}

.work-container h3 {
    margin-top: 28rem;
    text-align: center;
    padding: 0.5rem;
    width: 100%;
    background-color: white;
    color: black;
    opacity: 0.8;
    text-transform: uppercase;
}
.work-container h3 span {
    color: lightsalmon;
}
.work-container .workInfo {
    background-color: lightsalmon;
    height: 90px;
    width: 100%;
    opacity: 0.8;
    padding: 0.5rem;
}
.work-container a {
    margin-right: 20px;
    padding: 0.5rem 1rem;
    border-radius: 30px;
    border: 3px solid black;
    text-decoration: none;
}
.work-container a.first-btn {
    position: relative;
    top: 25rem;
    background-color: lightsalmon;
    height: 10px;
    width: 50px;
    color: black;
}
.work-container a.second-btn {
    position: relative;
    top: 25rem;
    background-color: white;
    height: 10px;
    width: 50px;
    color: black;
}
.work-container a.first-btn:hover {
    background-color: black;
    color: white;
}
.work-container a.second-btn:hover {
    background-color: black;
    color: white;
}

.slides {
    position: relative;
    height: 100px;
    width: 100px;
}
    .slick-prev, .slick-next {
      position: absolute;
      top: 40%;
    }
    .slick-prev {
        left: -10% !important;
        z-index: 1;
      }
    .slick-next {
        right: -8% !important;
        z-index: 1;
      }
    .slick-prev:before {
        font-size: 40px;
        color: white;
      }
      .slick-next:before {
        font-size: 40px;
        color: white;
      }

.img1 {
     background-image: url('images/portfolio1.png');
     background-size: 100% 100%;
     width: 50%;
}
.img2 {
    background-image: url('images/rps.png');
    background-size: 100% 100%;
    width: 50%;
}
.img3 {
    background-image: url('images/capstone.png');
    background-size: 100% 100%;
    width: 50%;
}
.img4 {
    background-image: url('images/netflix_clone.png');
    background-size: 100% 100%;
    width: 50%;
}

@media screen and (max-width: 500px) {
    .workPage {
        height: auto;
        font-size: 0.75rem;
    }
    .work-container h3 {
        font-size: 0.8rem;
    }
    .work-container {
        width: 100%;
        height: auto;
        position: relative;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }
    .work-container .workInfo {
        background-color: lightsalmon;
        width: 100%;
        opacity: 0.8;
        font-size: 0.75rem;
    }

}
@media screen and (max-width: 768px) {
    .workPage {
        height: auto;
        font-size: 1rem;
    }

    .work-container {
        width: 100%;
        height: auto;
        position: relative;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }
    .work-container .workInfo {
        background-color: lightsalmon;
        height: 5rem;
        width: 100%;
        opacity: 0.8;
    }
}

@media screen and (min-width: 769px) and (max-width: 1170px) {
    .workPage {
        height: auto;
        font-size: 1rem;
    }
    .workPage .workTitle {
        width: 90%;
    }
    .workPage .dash2 {
        width: 90%;
    }
    .work-container {
        width: 100%;
