
.skillcontainer {
    background-image: url('images/skillbackground.jpg');
    margin-top: 0;
    height: 950px;
    width: 100%;
    text-decoration: none;
    padding: 1.5rem;
    background-size: cover;
}
.skillcontainer h3 {
    margin-top: -1rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
    color: black;
    font-size: 2rem;
    text-transform: uppercase;
    text-align: center;

}
.skillbar {
    justify-items: center;
    margin: 1rem 2rem;
    display: grid;
    grid-gap: 20px;
    text-align: center;
}
.skillbar .box{
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    margin-top: 2rem;
    padding: 0.5rem 1rem;
    background-color: white;
    border: 2px solid black;
}

.html {
    background-size: cover;
    grid-column-start:1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
}
.js {
    background-size: cover;
    grid-column-start:2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
}
.python {
    background-size: cover;
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 2;
}
.php {
    background-size: cover;
    grid-column-start: 4;
    grid-column-end: 5;
    grid-row-start: 1;
    grid-row-end: 2;
}

.box .photo {
    border: 2px solid black;
    border-radius: 50%;
    height: 150px;
    width: 150px;
    position: relative;
    margin-left: auto;
    margin-right: auto;
}
.html .photo {
    margin-top: 1.5rem;
    background-image: url("images/sass.png");
    background-size: cover;
}
.js .photo {
    background-image: url("images/react.png");
    background-size: cover;
    margin-top: 1.5rem;
}
.python .photo {
    background-image: url("images/matplotlib.png");
    background-size: cover;
    margin-top: 1.5rem;
}
.php .photo {
    background-image: url("images/mysql.png");
    background-size: cover;
    margin-top: 1.5rem;    
}
.skillcontainer .box .title {
    color: black;
    text-transform: uppercase;
    font-size: 1.5rem;
}
.box .details1 {
    display: flex;
    padding-top: 2rem;
    padding-left: 1rem;
    padding-right: 1rem;
    font-weight: bolder;
    text-align: justify;
}
.skillcontainer .skilltitle {
    text-align: center;
    position: relative;
    margin-right: auto;
    margin-left: auto;
    margin-top: 2rem;
    color: black;
    padding: 0.5rem;
    width: 20rem;
    border: 2px solid white;
    font-size: 25px;
    font-weight: bolder;
    background-color: lightsalmon;
}
.skillcontainer .skilldash {
    position: relative;
    margin-right: auto;
    margin-left: auto;
    margin-top: 10px;
    background-color: white;
    width: 20rem;
    height: 3px;
    color: white;
}
.skillcontainer .dash {
    background-color: lightsalmon;
    display: block;
    margin-right: auto;
    margin-left: auto;
    margin-top: -0.5rem;
    height: 3px;
    width: 100px;
    text-align: center;
}

@media screen and (max-width: 500px) {
    .skillcontainer {
        height: auto;
    }
   .box {
    text-align: center;
    justify-content: center;
    display: block;
    grid-template-columns: 1fr; 
    grid-template-rows: repeat(4, 1fr);
    position: relative;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    }

    .html {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 1;
        grid-row-end: 2;
    } 
    .js {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 2;
        grid-row-end: 3;
    }
    .python {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 3;
        grid-row-end: 4;
    }
    .php {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 4;
        grid-row-end: 5;
    }
}

